const validationTerimaSupplier = (value) => {
  const errors = {};
  if (!value.kodeBarang) {
    errors.kodeBarang = "Tidak Boleh kosong, silahkan isi";
  }
  if (!value.pkg) {
    errors.pkg = "Tidak Boleh kosong, silahkan isi";
  }
  if (!value.p) {
    errors.p = "Tidak Boleh kosong, silahkan isi";
  }
  if (!value.k) {
    errors.k = "Tidak Boleh kosong, silahkan isi";
  }
  if (!value.bruto) {
    errors.bruto = "Tidak Boleh kosong, silahkan isi";
  }
  return errors;
};

export default validationTerimaSupplier;
