import React, { Component } from "react";
import axios from "axios";
import { Field, reduxForm, formValueSelector } from "redux-form";
import { connect } from "react-redux";
import validationTerimaSupplier from "../../Validations/validationTerimaSupplier";
import { createNumberMask } from "redux-form-input-masks";

const currencyMask = createNumberMask({
  prefix: "Rp. ",
  suffix: ",-",
  locale: "id-ID",
});

const renderField = ({
  input,
  label,
  type,
  limit,
  readOnly,
  meta: { touched, error, warning },
}) => (
  <div className="form-group">
    <label htmlFor="" className="text-black">
      {label}
    </label>
    <input
      {...input}
      type={type}
      className="form-control"
      min="0"
      step="0.001"
      maxLength={limit}
      readOnly={readOnly}
    />
    {touched &&
      ((error && <p className="invalid-feedback">{error}</p>) ||
        (warning && <p>{warning}</p>))}
  </div>
);
const server = process.env.REACT_APP_BACKEND_URL;

class FormEditPenerimaanSupplierNew extends Component {
  constructor(props) {
    super(props);
    this.onclick1 = this.onclick1.bind(this);
    this.state = {
      kodeBarang: [],
      DataSupplier: [],
      kode_barang: "",
      keterangan: "INI KETERANGAN",
      kadar: "",
      P: "",
      K: "",
      bungkus: "",
      berat: "",
      harga: "",
      murni: "",
      ongkos: "",
      status: "",
      kadarCetak: "",
      kadarBeli: "",
      namaJenis: "",
      kodeJenis: "",
    };
  }

  componentDidMount() {
    axios
      .get(server + "subcategorys")
      .then((res) =>
        this.setState({
          kodeBarang: res.data,
        })
      )
      .catch((err) => console.log(err));
  }

  onclick1() {
    this.props.change("netto", this.props.netto);
    this.props.change("noFaktur", this.props.noFaktur);
    this.props.change("gross", this.props.gross);
  }

  pilihBarang = (input) => (e) => {
    var supp = e.target.value
      ? e.target.value
      : "DEFAULT | DEFAULT | DEFAULT | DEFAULT ";
    var result_explode = supp.split("|");
    this.setState({
      kodeJenis: result_explode[0],
      namaJenis: result_explode[1],
      kadarCetak: result_explode[2],
      kadarBeli: result_explode[3],
    });
  };

  setKadar() {
    this.props.change("keterangan", this.state.namaJenis);
    this.props.change("kadar", this.state.kadarCetak);
    this.props.change("kadarTransaksi", this.state.kadarBeli);
  }

  render() {
    return (
      <form onSubmit={this.props.handleSubmit}>
        <div className="row">
          <div className="col-md-12 col-lg-12 d-none">
            <Field
              name="noFaktur"
              label={this.props.noFaktur}
              type="text"
              readOnly={true}
              component={renderField}
              defaultValue={this.props.noFaktur}
              normalize={(value) => value && value.toUpperCase()}
            />
          </div>
          <div className="col-md-4 form-group">
            <label htmlFor="">Kode Barang</label>
            <Field
              name="kodeJenis"
              component="select"
              className="form-control"
              onChange={this.pilihBarang()}
              onBlur={this.setKadar()}
            >
              {this.state.kodeBarang === [] ? (
                <option>TIDAK ADA DATA</option>
              ) : (
                this.state.kodeBarang.map((kode) => (
                  <option
                    value={
                      kode.kodeJenis +
                      "|" +
                      kode.namaJenis +
                      "|" +
                      kode.kadarCetak +
                      "|" +
                      kode.kadarBeli
                    }
                  >
                    {kode.namaJenis}
                  </option>
                ))
              )}
            </Field>
          </div>
          <div
            className="col-md-8 form-group"
            style={
              this.props.from === "terima" ? null : { visibility: "hidden" }
            }
          >
            <Field
              name="keterangan"
              label="Keterangan"
              type="text"
              component={renderField}
              normalize={(value) => value && value.toUpperCase()}
            />
          </div>
          <div className="col-md-3 form-group">
            <Field
              name="kadar"
              label="Kadar (%)"
              type="number"
              component={renderField}
              readOnly={true}
            />
          </div>
          <div className="col-md-3 form-group">
            <Field name="p" label="P" type="number" component={renderField} />
          </div>
          <div className="col-md-3 form-group">
            <Field name="k" label="K" type="number" component={renderField} />
          </div>
          <div className="col-md-3 form-group">
            <Field
              name="pkg"
              label="Bungkus (Gr)"
              type="number"
              component={renderField}
              onBlur={() => this.onclick1()}
            />
          </div>
          <div className="col-md-3 form-group">
            <Field
              name="bruto"
              label="Berat (Gr)"
              type="number"
              component={renderField}
              onBlur={() => this.onclick1()}
            />
          </div>
          <div className="col-md-3 form-group">
            <Field
              name="kadarTransaksi"
              label="Harga (%)"
              type="number"
              component={renderField}
              onBlur={() => this.onclick1()}
            />
          </div>
          <div className="col-md-3 form-group">
            <Field
              name="gross"
              label="Gross (Gr)"
              type="number"
              component={renderField}
              readOnly={true}
            />
          </div>
          <div className="col-md-3 form-group">
            <Field
              name="netto"
              label="Murni (Gr)"
              type="number"
              component={renderField}
              readOnly={true}
            />
          </div>
          <div className="col-md-3 form-group">
            <label htmlFor="">Ongkos (Rp)</label>
            <Field
              name="rp"
              component="input"
              type="tel"
              className="form-control"
              {...currencyMask}
            />
          </div>
          <div className="col-md-12 form-group mt-3">
            <div className="text-right">
              <button
                className="btn btn-primary"
                type="submit"
                disabled={this.props.submitting}
              >
                Simpan
              </button>
            </div>
          </div>
        </div>
      </form>
    );
  }
}

FormEditPenerimaanSupplierNew = reduxForm({
  form: "TerimaSupplier",
  validate: validationTerimaSupplier,
  enableReinitialize: true,
})(FormEditPenerimaanSupplierNew);
const selector = formValueSelector("TerimaSupplier"); // <-- same as form name

export default connect((state) => {
  const { bruto, kadarTransaksi, pkg } = selector(
    state,
    "bruto",
    "kadarTransaksi",
    "pkg"
  );
  return {
    netto: (parseFloat(bruto) * (parseFloat(kadarTransaksi) / 100)).toFixed(3),
    gross: parseFloat(bruto) + parseFloat(pkg),
  };
})(FormEditPenerimaanSupplierNew);
