import React, { Component } from "react";
import { connect } from "react-redux";
import { getsupplierList } from "../actions/supplierAction";
import Axios from "axios";
import { getUUID } from "../actions/fakturAction";
import { Link } from "react-router-dom";
import FormEditPenerimaanSupplierNew from "./Component/FormEditPenerimaanSupplierNew";

const server = process.env.REACT_APP_BACKEND_URL;

const maptostate = (state) => {
  return {
    supplierList: state.provinsi.supplier,
    noFaktur: state.provinsi.noFaktur,
    alert: state.provinsi.alert,
    error: state.provinsi.error,
  };
};

class editPenerimaanSupplierNew extends Component {
  constructor(props) {
    super(props);
    var date = new Date();
    var formatedDate = `${date.getFullYear()}-${
      date.getMonth() + 1
    }-${date.getDate()}`;
    this.state = {
      listSupplier: [],
      columns: [
        {
          dataField: "kodeBarang",
          text: "Kode Barang",
        },
        {
          dataField: "kadar",
          text: "Kadar Cetak(%)",
        },
        {
          dataField: "p",
          text: "P",
        },
        {
          dataField: "k",
          text: "K",
        },
        {
          dataField: "bungkus",
          text: "Bungkus(Gr)",
        },
        {
          dataField: "berat",
          text: "Berat(Gr)",
        },
        {
          dataField: "harga",
          text: "Harga(%)",
        },
        {
          dataField: "murni",
          text: "Murni(Gr)",
        },
        {
          dataField: "ongkosKirim",
          text: "Ongkos(RP)",
        },
      ],
      title: "PENERIMAAN SUPPLIER",
      content: "HELLO",
      tanggal_invoice: "",
      tanggal_barang: "",
      noBon: "",
      kodeSupplier: "",
      namaSupplier: "",
      startingDate: formatedDate,
      tampungBarang: [],
    };
  }

  componentDidMount() {
    this.props.dispatch(getsupplierList());
    Axios.get(server + "suppliers")
      .then((res) =>
        this.setState({
          listSupplier: res.data,
        })
      )
      .catch((err) => alert(err));
    this.props.dispatch(getUUID());
  }

  inputChange = (input) => (e) => {
    this.setState({
      [input]: e.target.value,
    });
  };

  pilihSupplier = (input) => (e) => {
    var supp = e.target.value ? e.target.value : "DEFALT | DEFAULT";
    var result_explode = supp.split("|");
    this.setState({
      kodeSupplier: result_explode[0],
      namaSupplier: result_explode[1],
    });
  };

  handleSubmit(data) {
    let isi = {
      noFaktur: this.props.noFaktur,
      tanggal: this.state.tanggal_invoice,
      noBon: this.state.noBon,
      kodeSupplier: this.state.kodeSupplier,
      namaSupplier: this.state.namaSupplier,
      tanggalBarang: this.state.tanggal_barang,
      barangDetails: {
        noFaktur: this.props.noFaktur, //
        nettoDiscGlobalExt: 0,
        gross: parseFloat(data.gross), //berat + bungkus
        netto: parseFloat(data.netto), //murni
        qty: 0,
        k: parseFloat(data.k),
        rp: parseFloat(data.rp), //ongkos
        kadarCetak: 0,
        keterangan: data.keterangan,
        discGlobalPr: 0,
        kodeKategori: "-",
        kadarTransaksi: parseFloat(data.kadarTransaksi), //harga%
        kodeJenis: data.kodeJenis, //kodebarang
        noBarcode: "-",
        p: parseFloat(data.p),
        discGlobalGr: 0,
        pkg: parseFloat(data.pkg),
        bruto: parseFloat(data.bruto), //berat
        nettoDiscGlobal: 0,
      },
    };
    console.log(isi);
    var array = this.state.tampungBarang;
    array.push(isi);
    this.setState({
      tampungBarang: array,
    });
  }

  render() {
    return (
      <div className="container-fluid" style={{ color: "black" }}>
        <div className="row">
          <div className="col-12">
            <ol className="breadcrumb mb-2">
              <Link to="/ayugold.com/editStok">
                <button type="button" className="btn btn-secondary">
                  <i className="fa fa-angle-left mr-3" aria-hidden="true"></i>{" "}
                  Back
                </button>
              </Link>
            </ol>
            <div className="card">
              <div className="card-body">
                <div className="col-lg-12 col-md-12">
                  <div className="row">
                    <div className="col-lg-4 col-md-4">
                      <label htmlFor="">Supplier</label>
                      <select
                        name="supplier"
                        id="kodeSupplier"
                        className="form-control"
                        onChange={this.pilihSupplier("kodeSupplier")}
                      >
                        <option value="DEFAULT | DEFAULT">
                          SILAHKAN PILIH
                        </option>
                        {this.state.listSupplier.map((kode) => (
                          <option
                            value={kode.kodeSupplier + "|" + kode.namaSupplier}
                          >
                            {kode.namaSupplier}
                          </option>
                        ))}
                      </select>
                    </div>
                    <div className="col-md-4 col-lg-4">
                      <label htmlFor="">Tanggal Invoice</label>
                      <input
                        type="date"
                        id="tanggal_invoice"
                        name="tanggal_invoice"
                        className="form-control"
                        defaultValue={this.state.startingDate}
                        onChange={this.inputChange("tanggal_invoice")}
                      />
                    </div>
                    <div className="col-md-4 col-lg-4">
                      <label htmlFor="">Tanggal Barang</label>
                      <input
                        type="date"
                        id="tanggal_barang"
                        name="tanggal_barang"
                        className="form-control"
                        onChange={this.inputChange("tanggal_barang")}
                      />
                    </div>
                    <div className="col-md-6 col-lg-4 mt-3">
                      <label htmlFor="">Keterangan</label>
                      <input
                        type="text"
                        id="keterangan"
                        name="keterangan"
                        className="form-control"
                        onChange={this.inputChange("keterangan")}
                      />
                    </div>
                    <div className="col-md-4 col-lg-4 mt-3">
                      <label htmlFor="">No Bon</label>
                      <input
                        type="text"
                        id="noBon"
                        name="noBon"
                        className="form-control"
                        onChange={this.inputChange("noBon")}
                      />
                    </div>
                    <div className="col-md-12 col-lg-12 mt-3">
                      <div className="row">
                        <div className="col-lg-6 col-md-6">
                          <div className="text-left">
                            <Link to="/ayugold.com/ViewPDF">
                              <button className="btn btn-primary" type="button">
                                PDF
                              </button>
                            </Link>
                          </div>
                        </div>
                        <div className="col-lg-6 col-md-6">
                          <div className="text-right">
                            <button
                              className="btn btn-primary"
                              data-toggle="modal"
                              data-target="#primary-header-modal"
                            >
                              Tambah Data
                            </button>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div
          id="primary-header-modal"
          className="modal fade"
          tabIndex="-1"
          role="dialog"
          aria-labelledby="primary-header-modalLabel"
          aria-hidden="true"
        >
          <div className="modal-dialog modal-lg">
            <div className="modal-content">
              <div className="modal-header modal-colored-header bg-primary">
                <h4 className="modal-title" id="primary-header-modalLabel">
                  Tambah Data Terima Supplier
                </h4>
                <button
                  type="button"
                  className="close"
                  data-dismiss="modal"
                  aria-hidden="true"
                >
                  Ã—
                </button>
              </div>
              <div className="modal-body">
                <FormEditPenerimaanSupplierNew
                  from="terima"
                  onSubmit={(data) => this.handleSubmit(data)}
                  noFaktur={this.props.noFaktur}
                />
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default connect(maptostate, null)(editPenerimaanSupplierNew);
